/* variables*/
$color_1: $textColor;
$color_4: #999;
$color_5: rgba(0, 0, 0, .4);
$color_6: $textColor;
$color_7: #444;
$color_8: #fff;
$color_9: $color-warm-grey;
$color_10: $color-warm-grey;
$color_11: $color_8;
$color_12: $color-minsk;
$background_color_1: #f4f3f0;
$background_color_2: #f6f4f9;
$background_color_3: $color_8;
$background_color_4: rgba(255, 255, 255, 0.4);
$background_color_5: $color-minsk;
$background_color_6: $color-minsk;
$background_color_7: $color-atlantis;
$background_color_8: $color-alizarin;
$background_color_9: $color-pumpkin;
$background_color_10: $color-pumpkin;
$background_color_11: $color-alizarin;
$background_color_12: $color-alizarin;
$border_color_1: #d8d7d4;
$border_color_2: $color-minsk;
$border_color_3: #d8d7d3;
$border_top_color_1: #e5e5e5;

body.app-public.namespace-newsroom {
    margin: 0;
    padding: 0px;
    font-size: 16px;
    color: $color_1;
    background-color: $background_color_1;
    .markdown {
        font-size: 16px;
    }
}

.article {
    color: $color_1;
    background: $color_8;
    padding: 10px;
    .date-large {
        background: $background_color_1;
        .dtstart {
            background: $color_8;
            .day {
                color: $color-minsk;
            }
            .time {
                color: $color-minsk;
            }
        }
    }
    .related-materials {
        h4 {
            font-size: 20px;
            margin-bottom: 4px;
            color: $color-minsk;
        }
        li {
            font-size: 14px;
        }
    }
    .newsroom-list-header {
        padding-top: 0;
    }
    .header {
        padding: 0;
    }
}

.newsroom-article {
    .markdown {
        b {
            color: $color_1;
        }
        strong {
            color: $color_1;
        }
        .boilerplate {
            font-size: 16px;
            color: $color_6;
            border-color: $border_color_1;
        }
        table {
            border-color: $border_color_3;
            background: $color_8;
            font-size: 14px;
            td {
                font-size: 14px;
                word-break: break-all;
            }
            th {
                font-size: 14px;
                word-break: break-all;
            }
            tr {
                &:nth-child(even) {
                    td {
                        background: #f6f4f8;
                    }
                }
            }
        }
    }
    .event-time-location {
        background: $color_8;
        border: 1px solid #d8d7d3;
    }
    .event-info {
        font-size: 16px;
        line-height: 1.5;
        margin-bottom: 20px;
    }
    .table-bordered {
        line-height: 1.5;
        font-size: 14px;
        border-color: $border_color_3;
        background: $color_8;
        td {
            line-height: 1.5;
            font-size: 14px;
            padding: 12px;
        }
        th {
            background: #f6f4f8;
            color: $color_6;
            padding: 12px;
        }
    }
}

.newsroom-header-content {
    .newsroom-logotype {
        padding-top: 20px;
        padding-bottom: 0;
    }
    .newsroom-nav-collapse {
        background: $color-cararra;
        border: 0;
    }
    .newsroom-search {
        input[type="text"] {
            margin-left: 0;
            box-sizing: border-box;
        }
    }
}

.newsroom-header-misc.hide-hosted-header {
    right: 0;
    bottom: 45px;
    bottom: 52px;
}

.ie7 {
    .newsroom-header-content {
        .newsroom-nav-collapse {
            padding-top: 40px;
        }
    }
    .newsroom-header-misc.hide-hosted-header {
        bottom: 85px;
    }
    #hosted-footer {
        .footer {
            border-top-color: $border_top_color_1;
            a {
                color: $color_4;
            }
        }
    }
}

.newsroom-logotype {
    .logotype {
        margin-bottom: 20px;
    }
}

h1 {
    font-weight: normal;
    margin: 0;
    padding: 0;
    font-size: 50px;
    padding-bottom: 15px;
    padding-top: 15px;
    color: $color-minsk;
    small {
        color: $color-minsk;
    }
}

h2 {
    color: $color_7;
    font-weight: normal;
    margin: 0;
    padding: 0;
    padding-bottom: 15px;
    padding-top: 15px;
    small {
        color: $color-minsk;
    }
}

h3 {
    color: $color_7;
    font-weight: normal;
    margin: 0;
    padding: 0;
    small {
        color: $color-minsk;
    }
}

h4 {
    color: $color_7;
    font-weight: normal;
    margin: 0;
    padding: 0;
    small {
        color: $color-minsk;
    }
}

h5 {
    color: $color_7;
    font-weight: normal;
    margin: 0;
    padding: 0;
    small {
        color: $color-minsk;
    }
}

h6 {
    color: $color_7;
    font-weight: normal;
    margin: 0;
    padding: 0;
    small {
        color: $color-minsk;
    }
}

.event-date-badge {
    color: $color-minsk !important;
}

.newsroom-headline {
    font-size: 30px !important;
    font-weight: 400 !important;
    font-size: 35px !important;
}

.newsroom-section-header {
    font-size: 24px;
    font-weight: 400;
    text-transform: none;
    color: $color-minsk;
    font-size: 35px;
    text-align: right;
    border: 0;
    small {
        a {
            color: $color-alizarin !important;
            font-size: 18px;
            &:hover {
                color: $color-alizarin !important;
                text-decoration: underline;
            }
        }
    }
    a {
        color: $color-minsk;
        font-size: 35px;
        text-align: right;
        border: 0;
    }
    span {
        color: $color-minsk;
        font-size: 35px;
        text-align: right;
        border: 0;
    }
}

.newsroom-list-header {
    color: $color-minsk;
    font-size: 16px;
    font-weight: 400;
    font-size: 20px;
    a {
        text-decoration: none;
        color: $color-alizarin !important;
        &:hover {
            text-decoration: underline;
            color: $color-alizarin !important;
            text-decoration: underline;
        }
    }
    &:hover {
        color: $color-minsk;
    }
}

ul {
    li {
        color: $color_1;
    }
    margin: 0;
    padding: 0 0 10px 0;
    list-style-type: disc;
}

a {
    color: $color-alizarin;
    text-decoration: none;
    &:hover,
    &:focus,
    &:active,
    &:visited {
        text-decoration: underline;
        color: $color-alizarin;
    }
}

.newsroom-nav-item.active {
    >a {
        color: $color-minsk;
    }
    background: transparent;
    border: 0;
}

.newsroom-nav-item {
    >a {
        &:hover {
            color: $color-minsk;
            color: $color_8;
            background: $color-minsk;
            opacity: 1;
        }
        padding: 10px 10px;
        color: $color-minsk;
        font-size: 14px;
    }
    padding: 0;
    margin: 0;
}

.article-module {
    overflow: hidden;
    a {
        text-decoration: none;
        &:hover {
            text-decoration: none;
        }
    }
    background: $color_8;
    text-align: right;
    font-size: 16px;
    line-height: 24px;
    li {
        font-size: 16px;
        line-height: 24px;
    }
    p {
        font-size: 16px;
        line-height: 24px;
    }
    .article-module-with-text {
        background: $color_8;
        .article-module-text {
            background: $color_8;
            .meta {
                font-size: 12px;
                color: $color_6;
            }
        }
        .article-module-read-more {
            a {
                background: $color_8;
                &:hover {
                    background: $color_8;
                    text-decoration: underline;
                }
            }
            .article-module-read-more-fade {
                display: block;
                background-color: $background_color_4;
                background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), $color_8);
                background-repeat: repeat-x;
                filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffffff', endColorstr='$color_8', GradientType=0);
            }
        }
    }
    .article-module-with-image {
        .article-module-text {
            background: $color_8;
            .newsroom-list-header {
                font-size: 20px;
                line-height: 19px !important;
            }
            .meta {
                font-size: 12px;
                color: $color_6;
            }
        }
        .article-module-text-inner {
            >.hidden-small {
                display: none;
            }
        }
    }
    .article-module-navigation {
        li {
            border-left: 0;
            color: $color-light;
            transition: all 0.3s ease;
            background: $color-minsk;
            border-bottom: 0px transparent;
            display: block;
            padding: 1em 1em;
            margin: 0;
            opacity: 1;
            &:hover {
                background-color: $color-jacarta;
            }
            .newsroom-list-header {
                a {
                    font-weight: normal;
                    color: $color-light !important;
                    opacity: 1;
                }
            }
        }
        .article-module-thumb {
            display: none;
        }
        li.selected {
            background: $color-jacarta;
            border-left: 0;
            border-bottom: 0 transparent;
            &:hover {
                background-color: $color-jacarta;
            }
        }
        .meta {
            color: $color-light;
            font-size: 14px;
        }
        .newsroom-list-header {
            font-weight: normal;
            padding-top: 0;
            line-height: 21px;
            padding-bottom: 6px;
            font-size: 16px;
        }
    }
    .article-module-slide {
        .newsroom-list-header {
            font-size: 35px;
            padding: 0;
            a {
                color: $color_7 !important;
            }
        }
    }
    .meta {
        font-size: 12px;
        color: $color_6;
    }
    .article-module-read-more {
        text-align: right;
    }
    .markdown {
        text-align: right;
    }
    > .row-fluid {
        > .span8 {
            width: 2.12766% + 65.95745%;
            @media (max-width: 767px) {
                width: 100%;
            }
        }
        > .span4 {
            margin-right: 0;
        }
    }
    ul {
        padding-right: 16px;
    }
    ol {
        padding-right: 16px;
    }
}

.article-module-read-more-fade {
    display: none;
}

.article-latest {
    padding-bottom: 99em;
    margin-bottom: -99em;
    background-color: $color-minsk;
}

#license {
    a {
        &:hover {
            span {
                border: 0;
            }
        }
    }
}

#hosted-footer {
    .footer {
        margin-top: 15px;
        padding-top: 15px;
        padding-bottom: 40px;
        color: $color_4;
        font-size: 11px;
        text-align: center;
        border-top: solid 1px rgba(0, 0, 0, 0.06);
        a {
            color: $color_5;
            text-decoration: underline;
        }
    }
    div.inner {
        padding-top: 0;
    }
}

.ie8 {
    #hosted-footer {
        .footer {
            border-top-color: $border_top_color_1;
            a {
                color: $color_4;
            }
        }
    }
}

div.header-bar {
    border: 0;
}

body {
    font-size: 16px;
    line-height: 24px;
    background: $background_color_1;
    color: $color_6;
    width: 100%;
}

textarea {
    background: $color_8;
    border: none;
    color: $color_6;
    padding: 7px 20px;
    margin-left: 10px;
    font-size: 16px;
    height: auto;
}

.page {
    text-align: right;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ol {
    margin: 0;
    padding: 0 0 10px 0;
}

dl {
    margin: 0;
    padding: 0 0 10px 0;
}

.clear {
    display: block;
    height: 1%;
    &:after {
        clear: both;
        content: ".";
        display: block;
        height: 0;
        visibility: hidden;
    }
}

hr {
    background: transparent;
    border: 0;
    border-bottom: 1px dotted #ccc;
    height: 0;
    margin: 0;
}

li {
    font-size: 16px;
    line-height: 24px;
    margin: 0 2px 0 0;
    padding: 0;
}

.header {
    background: $color_8;
}

.main {
    background: $color_8;
    z-index: 5;
    position: relative;
}

.pageConfine {
    margin: 0 auto 0 auto;
    position: relative;
    max-width: 940px;
    z-index: 2;
}

.pageConfineMenu {
    margin: 0 auto 0 auto;
    max-width: 940px;
}

.logo {
    float: right;
    margin: 0;
    padding: 0;
    width: 393px;
    line-height: 0;
    a {
        width: 393px;
        max-width: 100%;
