            font-size: 16px;
            padding-bottom: 4px;
        }
        .newsroom-list-header {
            font-size: 28px;
            padding: 0;
            margin-bottom: 0;
            a {
                color: $color-minsk !important;
            }
        }
    }
    .vcard {
        margin-bottom: 20px;
        .span3.m-span4 {
            margin-right: 0;
        }
