    }
}

.show-more-tags>.btn-mini{
    margin-top: 0;
    margin-bottom: 10px;
}

.tag-filters {
    .btn-mini {
        @extend %chips;
        margin: 0 0 10px 10px;
        font-size: 16px !important;
        text-shadow: none;
        &:after{
            display: none;
        }
        b {
            font-weight: bold;
        }
        &:hover {
            color: $color-light;
            background-color: $color-jacarta;
            border-color: $color-jacarta;
        }
    }
    .btn-mini.selected {
        color: $color-light;
        background-color: $color-jacarta;
        border-color: $color-jacarta;
    }
}

.highlighted-tag.media-wrapper {
    width: 100%;
    & > a{
        display: block;
    }
    img {
        margin: 0 auto;
    }
}

.list-material {
    .row-contacts {
        .even {
            margin-bottom: 20px;
        }
        li {
            font-size: 16px;
            &:first-child {
                .role {
                    padding-bottom: 0;
                }
            }
        }
        .role {
            color: $color_1;
            font-size: 16px;
            padding-bottom: 8px;
            display: inline-block;
        }
        .hidden-small {
            display: none;
        }
    }
    .newsroom-section {
        .row-fluid {
            .article.span6 {
                display: table-cell;
                height: 100%;
                float: none;
                border-bottom: 20px solid $background_color_1;
                border-right: 10px solid $background_color_1;
                vertical-align: top;
                &:first-child {
                    border-left: 10px solid $background_color_1;
                    border-right: 0;
                    &:last-child {
                        border: 0;
                        float: right;
                    }
                }
