    #comment-submit {
        padding: 7px 20px;
    }
    .alert-info {
        border-radius: 2px 2px 2px 2px;
        display: block;
        margin-bottom: 20px !important;
        background: #e0e0c0;
        text-align: center;
        line-height: 1.5;
        border: 0;
        filter: none;
        padding: 12px;
    }
    overflow: hidden;
}

form {
