@charset "UTF-8";

/*
    Media Queries Mixins

    small-mobile             === (max-width: 479px)
    large-mobile             === (min-width: 480px) and (max-width: 767px);

    media-only-mobile        === (max-width: 767px);
    media-only-tablet        === (min-width: 768px) and (max-width: 1023px);
    media-only-dekstop       === (min-width: 1024px) and (max-width: 1299px);
    media-only-dekstop-lg    === (min-width: 1200px);

    media-from-mobile        === (min-width: 480px);
    media-from-tablet        === (min-width: 768px);
    media-from-desktop       === (min-width: 1024px);
    media-from-desktop-lg    === (min-width: 1300px);

*/

@mixin media-small-mobile {
    @media (max-width: #{$screen-xs - 1}) {
        @content;
    }
}

@mixin media-large-mobile {
    @media (min-width: #{$screen-xs}) and (max-width: #{$screen-xs-max}) {
        @content;
    }
}

@mixin media-only-mobile {
    @media (max-width: #{$screen-xs-max}) {
        @content;
    }
}
@mixin media-only-mobile2 {
    @media (max-width: #{$screen-sm}) {
        @content;
    }
}

@mixin media-only-tablet {
    @media (min-width: #{$screen-sm}) and (max-width: #{$screen-sm-max}) {
        @content;
    }
}

@mixin media-only-desktop {
    @media (min-width: #{$screen-md}) and (max-width: #{$screen-md-max}) {
        @content;
    }
}

@mixin media-only-desktop-lg {
    @media (min-width: #{$screen-lg}) {
        @content;
    }
}

@mixin media-from-mobile {
    @media (min-width: #{$screen-xs}) {
        @content;
    }
}

@mixin media-to-tablet {
    @media (max-width: #{$screen-xs-max}) {
        @content;
    }
}

@mixin media-from-tablet {
    @media (min-width: #{$screen-sm}) {
        @content;
    }
}

$screen-lgtab: 960px; 
@mixin media-to-lgtablet {
    @media (max-width: #{$screen-lgtab - 1}) {
        @content;
    }
}
@mixin media-from-lgtablet {
    @media (min-width: #{$screen-lgtab}) {
        @content;
    }
}

@mixin media-only-lgtablet-to-desktop {
    @media (min-width: #{$screen-lgtab}) and (max-width: #{$screen-sm-max}) {
        @content;
    }
}

@mixin media-to-desktop {
    @media (max-width: #{$screen-sm-max}) {
        @content;
    }
}

@mixin media-from-desktop {
    @media (min-width: #{$screen-md}) {
        @content;
    }
}

@mixin media-from-desktop-lg {
    @media (min-width: #{$screen-lg}) {
        @content;
    }
}

@mixin mobile-or-touchable {
    @media(max-width: #{$screen-sm-max}), (pointer:coarse) {
        @content;
    }

}
@mixin pointer-coarse {
    @media (pointer: coarse) {
        @content;
    }
}

