%box-base-white,
.box-base-white {
    position: relative;
    margin-top: ($margin-default * 0.75);
    margin-bottom: $margin-default;
    padding: ($margin-default * 0.75) ($margin-default * 0.75) $margin-default;
    box-sizing: border-box;
    background-color: $color-light;
    background-position: left bottom;
    background-repeat: no-repeat;
    a {
        display: inline-block;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        text-decoration: none;
        vertical-align: bottom;
        width: auto;
    }
    .expert {
        @extend %h3;
    }
    &.solid-b, &.solid-b--purple {
        background-color: $color-alizarin;
        color: $color-light;
        a, h2, .sectionTag, .category {
            color: inherit;
        }
        a {
            @include hover {
                text-decoration: underline;
            }
        }
    }
    &.solid-b--purple {
        background-color: $color-minsk;
    }
}
%sml-cont,
%med-cont,
%lrg-cont {
    @extend %box-base-white;
    max-height: $height-promoblocks;
    margin: 0 0 $margin-default 0;
    overflow-wrap: break-word;
    word-wrap: break-word;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
}
%sml-cont {
    min-height: $height-promoblock-sml;
    .withSecondaryNav & {
        min-height: ($height-promoblock-sml - $base-size);
    }
}
%med-cont,
%lrg-cont {
    min-height: $height-promoblocks;
    .withSecondaryNav & {
        min-height: ($height-promoblocks - ($base-size * 2));
    }
}
%overlay,
.overlay {
    background: rgba(0, 0, 0, 0.7);
    height: 100%;
    right: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 80;
}
%yt-container,
.youtube-container {
    top: 50%;
    right: 50%;
    -ms-transform: translate(50%, -50%) scale(0.9);
    transform: translate(50%, -50%) scale(0.9);    
    position: fixed;
    padding-top: 56.25%;
    width: 100%;
    z-index: 101;
    iframe {
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        position: absolute;
        width: 100%;
        height: 100%;
    }
}
%filter-bar {
    @extend %clear;
    margin-top: 0; 
    margin-bottom: 0; 
    background: $color-minsk;
    color: $color-light;
    overflow: hidden;
    h2.title {
        display: -ms-flexbox; display: flex;
        -ms-flex-negative: 0;
        flex-shrink: 0;
        font-size: $size-x-large;
        line-height: 2.2em;
        vertical-align: top;
        margin: ($margin-default * .75) 0 0 $margin-default; 
        @include media-only-mobile {            
            width: 100%;            
        }
        @include rtl {
            margin: ($margin-default * .75) 0 0 $margin-default;
        }
    }
    @include media-from-tablet {
        input, button {
            margin: 0;
        }
    }
   [class*="col"] {
        @include media-only-mobile {
            div,button {
                display: block;
            }
            .form-select select {
                width: 100%;
            }           
            input {
                margin-bottom: ($margin-default / 2);
            }           
        }
        @include media-from-tablet {
            button, a {
                margin: 0;
            }
        }
    }    
    div.define-filter {
        display: -ms-flexbox; display: flex;
        -ms-flex-positive: 1;
        flex-grow: 1;
        margin-top: ($margin-default * .75);
        margin-bottom: ($margin-default * .75);
        button {
            -ms-flex-negative: 0;
            flex-shrink: 0;
        }        
        @include media-only-mobile {
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
            -ms-flex-direction: column;
            flex-direction: column;
            -ms-flex-positive: 0;
            flex-grow: 0;
            width: 32rem;
            margin-bottom: ($margin-default / 2);
            button {
                background: $color-light;
                color: $color-jacarta;                
                @include hover {
                    color: $color-light;
                    &:after {
                        background-color: $color-minsk;
                    }
                    @include pointer-coarse{
                        color: $color-jacarta;
                        &:after {
                            display: none;
                        }
                    }
                }
            }
        }
        @include media-small-mobile {
            -ms-flex-positive: 1;
            flex-grow: 1;
            width: 100%;
        }
    }
    div.define-search {
        display: -ms-flexbox; display: flex;
        -ms-flex-align: start;
        align-items: flex-start;
        @include media-only-mobile {
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        }
        .js-define-search {
            display: inline-block;
            display: -ms-flexbox; display: flex;
            -ms-flex-positive: 1;
            flex-grow: 1;
            -ms-flex-align: end;
            align-items: flex-end;
            padding-top: 0;
            padding-right: 0;
            margin: 0;
            -ms-flex-pack: start;
            justify-content: flex-start;
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
            margin-bottom: -.1rem;
            li {
                display: inline-block;
                margin-top: 4rem;
                margin-bottom: 4rem;
                a {
                    position: relative;
                    text-decoration: none;
                    color: $color-light;
                    font-size: $size-regular;
                    font-weight: 700;    
                    padding: 5rem $padding-default;                   
                    white-space: nowrap;
                    &.active,
                    &:hover {
                        background-color: $color-jacarta;                                     
                    }
                }

            }
        }
    }
}
%filter-bar-wrapper,
.filter-bar-wrapper {
    display: block;
    background: $color-jacarta;
    color: $color-light;
}
%filters-container,
.filters-container {
    overflow: hidden;
    ul {
        list-style: none;
    }    
    padding: $padding-default 0 ($padding-default / 2);
    li {
        background-color: $color-minsk;
        margin-bottom: ($margin-default / 2);
        border-radius: 0.4rem;
        &.open {
            .js-show-selection {
                &:before {
                    display: none;
                }
            }
        }
    }
    a {
        display: inline-block;
        background-color: transparent;
        text-decoration: none;
        font-size: $size-tiny;        
        padding: ($padding-default / 8) ($padding-default / 2);
        width: 25rem;
        color: $color-light;
        vertical-align: middle;
        position: relative;
        border-radius: 0.4rem;
        &.js-show-selection {            
            width: 3rem;           
            text-indent: -999em;
            padding: 0;
            &:after,
            &:before {
                position: absolute;
                content: '';
                width: 1.5rem;
                height: 0.1rem;
                background-color: $color-light;
                right: 8px;
                top: 50%;
            }
            &:before {
                -ms-transform: rotate(90deg);
                transform: rotate(90deg);                
            }
        }
    }
    .third-level-filters {
        li,a {
            background: transparent;
            color: $color-light;
            margin: 0;
        }
        a:hover {
            text-decoration: underline;
        }
    }
}
%topSpacingContainer,
.topSpacingContainer,
.relatedContainer{
    margin-top: $margin-default;
}
%smallerContainer,
.smallerContainer,
.error-404-container {
    @extend %container;
    margin-top: (2 * $margin-default) !important;
    margin-bottom: (2 * $margin-default);
}
.error-404-container {
    p {
        margin: initial;
    }
}
