@mixin retina {
	@media	(-webkit-min-device-pixel-ratio: 1.5),
			(   -moz-min-device-pixel-ratio: 1.5),
			(        min-device-pixel-ratio: 1.5) { 

		@content;		  
	}
}

/* Specify font size in px and also in rems for browsers that support it*/
/* Base font size on html should be set to 62.5% (10px)*/
@mixin font-size($sizeValue) {
	font-size: ($sizeValue / 10) + rem;
}

@mixin line-height($sizeValue) {
	line-height: ($sizeValue / 10) + rem;
}


@mixin accelerate {
	-webkit-transform: translate3d(0, 0, 0);
}

@mixin unselectable {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}


@mixin rtl {
	[dir="rtl"] &{
		@content;
	}
}

@mixin placeholder {
  &:-ms-input-placeholder {
  	@content
  }
  &::placeholder {
  	@content
  }
}

@mixin form-select($select-font-size: $size-regular) {    
    position: relative;
    display: inline-block;
    /*margin-top: -0.75 * $margin-default;*/
    max-width: 100%;
    select {        
        width: initial;
        max-width: 100%;
        color: $color-minsk;            
        background: $color-light;
        -webkit-appearance:none;
        -moz-appearance: none;                    
        font-size: $select-font-size;      
        padding: 1rem 2rem 1rem 4rem;        
        border: 0.2rem solid $color-minsk;
        border-radius: 0;      
        line-height: 2.8rem;  
    }
    &:after {
        content: '';   
        display: inline-block;                     
        border-right: 4px solid transparent;
        border-left: 4px solid transparent;
        border-top: 7px solid $color-minsk;                
        pointer-events: none;
		position:absolute;
        left: 2rem;
        top: 50%;
        -ms-transform: translate(0, -30%);
        transform: translate(0, -30%);
    }

    &--light {
        select {
            color: $color-light;
            border-color: transparent;
            background: none;

            option {
                color: $color-dark;
                background: $color-light;
            }
        }
        
        &:after {
            border-top-color: $color-light;
        }
    }

    @include media-only-mobile2 { 
        width: 100%;  
        max-width: 100%;         
        select {
            max-width: inherit;
            width: inherit;        
        }
    }
}