html, body {
    background: $color-warm-grey;
    position: relative;
    overflow-x: hidden;
    width: 100%;
    min-height: 100%;
    max-width: 100%;
    direction: rtl;
}
.overflowHidden, 
.overflowHidden body{
    overflow: hidden;
}
.main {
    background: $color-warm-grey;
    min-height: 100%;
    overflow: hidden;
}
@include media-to-lgtablet {
    .hide-mobile{
        visibility: hidden;
    }
}
@include media-from-lgtablet{
    .show-desktop{
        display:block;
    }
}
img {
    display:block;
    margin:0;
    padding:0;
    max-width:100%;
    height:auto;
}
%clear, .clear{
    &:after{
        content: "";
        display: table;
        clear: both;
    }
}
%hidden, .hidden{
    display: none;
}
%hide-on-mobile, .hide-on-mobile {
    @include media-only-mobile {
        display: none !important;
    }
}
%hide-to-desktop, .hide-to-desktop {
    @include media-to-desktop {
        display: none !important;
    }
}
hr.divider{
    margin: $margin-default 0;
    border: 0;
    border-top: 1px solid $color-rolling;
}
.op-0 {
    visibility: hidden;
}
.absolute {
    position: absolute;
    overflow: hidden !important;
}
