footer {
    background: $color-minsk;
    padding: ($padding-default * 4) 0 ($padding-default * 3) 0;
    color: $color-light;
    @include media-only-mobile {
        padding: (2 * $padding-default) 0;
    }
    .social-media-float {
        padding-right: 0;
        padding-left: 0;
        @include media-only-mobile {
            padding: 0 ($padding-default / 2) $padding-default ($padding-default / 2);
        }
        @include media-only-tablet {
            padding-bottom: ($padding-default * 2);
        }
        @include media-from-tablet {
            float: left;
        }
        .social-media {
            font-size: $size-large;
            color: $color-light;
            width: 100%;
            margin: 0;
            @include media-only-mobile {
                text-align: right;
            }
            .social-media-list {
                display: table;
                width: 100%;
                @include media-only-mobile {
                    text-align: right;
                }
                li {
                    display: table-cell;
                    @include media-only-tablet {
                    padding-right: 16px;
                    padding-left: 16px;
                    }                                
                    a {
                        @include media-only-tablet {
                            min-width: ($margin-default * 2);
                        }
                        display: inline-block;
                        color: $color-light;
                        text-decoration: none;
                        cursor: pointer;
                        svg {
                            display: block;
                            height: 3rem;
                            fill: currentColor !important;
                            path, rect {
                                fill: currentColor !important;
                            }
                        }
                    }
                }
                img, svg{
                    width: auto;
                    max-width: 100%;
                }
            }
        }
    }
    .link-list {
        padding-right: 0;
        padding-left: 0;
        a {
            font-size: $size-regular;
            color: $color-light;
            @include media-from-tablet {
                @include transition;
            }
            @include hover {
                color: $color-alizarin;
            }
        }
    }
    .copyright {
        padding-top: ($padding-default * 2);
        color: $color-light;
        @include media-only-mobile {
            padding-top: $padding-default;
        }
    }
    ul {
        padding: 0;
        margin: 0;
        @include media-only-mobile {
            padding-bottom: $padding-default;
        }
    }
    li {
        list-style: none;
        margin: 0 0 ($margin-default / 2) 0;
        span {
            font-size: $size-regular;
            color: $color-light;
            text-decoration: none;
            text-transform: uppercase;
            font-weight: 700;
        }
    }
}