.pagination {
    &-clean {
        li {
            a {
                height: 12px;
                width: 12px;
                background-color: $color-cararra;
                border: solid 1px $color-cararra;
                box-shadow: none;
                &:hover {
                    background-color: $color-minsk;
                    border: solid 1px $color-minsk;
                }
            }
        }
        li.current {
            a {
                background-color: $color-minsk;
                border: solid 1px $color-minsk;
            }
        }
    }
    ul {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        -ms-flex-pack: center;
        justify-content: center;
        box-shadow: none; 
        >li {
            display: block;
            margin: 0;
            width: 34px;
            max-width: 8%;
            &:after {
                content: ' ';
                display: table;
                clear: both;
            }
            >a {
                padding-right: 0;
                padding-left: 0;
                width: 100%;
                display: block;
                text-align:center;
            }
        }
    }
}