.mega {
    position: relative;
    z-index: 2;
    display: block;
    @include media-from-lgtablet {
        z-index: 11;
    }
    &__level {
        &--1 {
            box-shadow: inset 0 1px 0 0 $color-warm-grey;
            background: $color-light;
            position: relative;
            z-index: 2;
            .nav-link {
                padding: ($padding-default / 2) 0 (($padding-default / 2) - 2px);
            }
        }
    }
    &__content {
        position: relative;
        z-index: 1;
        span {
            a {
                text-decoration: none;
            }
        }
        @include media-from-lgtablet {
            .mobile__mode {
                display: none;
            }
        }
    }
    &__feature {
        padding-top: $padding-default;
        @include media-to-lgtablet {
            display: none;
        }
    }
    h3,
    .h3 {
        margin-top: -($margin-default / 4);
        margin-bottom: $margin-default;
        display: block;
        a {
            color: $color-dark;
            text-decoration: none;
            @include transition;
            @include hover {
                color: $color-alizarin;
            }
        }
    }
    .list-container {
        width: 100%;
    }
    .list--mega li {
        float: none;
        display: block;
        @include media-from-lgtablet {
            float: right;
        }
    }
}


/*GENERIC STYLES MOVE TO A BETTER HOME */


/*Full width 2 color background with containers content magic */

.container.bleed {
    position: relative;
    overflow: visible;
    &:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 50%;
        width: 100vw;
        height: 100%;
    }
    .row {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
    }
    [class*="col"] {
        position: relative; /*min-height: calc(100vh - 192px);*/
        direction: rtl;
    }
}

.bleed {
    display: none;
    &.open {
        display: block;
    }
    &__col {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
        padding: ($padding-default * 2) 0 $padding-default 0;
        &:before {
            content: '';
            position: absolute;
            height: 100%;
            width: 100vw;
            top: 0;
        }
        &--left:before {
            left: 0;
            background: $color-cararra;
        }
        &--right:before {
            right: 0;
            background: $color-warm-grey;
        }
    }
}


/* NAV LIST & LINK STYLING */

.list {
    margin: 0;
    padding: 0;
    &--inline {
        list-style-type: none;
        li {
            display: block;
            float: right;
        }
    }
    &--unstyled {
        list-style-type: none;
        li {
            display: block;
            position: relative;
        }
    }
    &--mega {
        li {
            margin: 0 ($margin-default * 0.5);
        }
    }
}

%nav-link {
    color: $color-minsk;
    font-size: $size-regular;
    text-decoration: none;
    display: block;
    padding: ($padding-default * 0.333) 0 (($padding-default * 0.5) - 2px);
    position: relative;
    word-break: break-word;
    @include transition;
    &:visited {
        color: $color-minsk;
        text-decoration: none;
    }
}

.nav-link {
    @extend %nav-link;
    border-bottom: 2px solid transparent;
    .mobile__level & {
        &:not(.header) {
            font-size: $size-large;
        }
    }
    @include media-from-lgtablet {
        .mega & {
            padding: 0;
            line-height: (($base-size * 2) - 2px);
            min-height: ($base-size * 2);
        }
        @include hover {
            border-bottom: 2px solid $color-minsk;
            color: $color-minsk;
            text-decoration: none;
        }
    }
}

.mega-link {
    font-size: $size-regular;
    color: $color-minsk;
    text-decoration: none;
    display: block;
    padding: 0 0 ($padding-default * 0.5) ($padding-default * 2);
    @include transition;
    @include hover {
        color: $color-alizarin;
    }
    &--level-2 {
        color: $color-dark;
        text-align: left;
        @include hover {
            color: $color-alizarin; /* color: $color-minsk; */
        }
        &:after {
            content: '';
            background: $color-warm-grey;
            display: block;
            opacity: 0;
            position: absolute;
            left: 0;
            top: 0;
            height: 15px;
            width: 15px;
            -ms-transform: rotate(-45deg) skewX(5deg) translateX(-70%);
            transform: rotate(-45deg) skewX(5deg) translateX(-70%);
            @include transition;
        }
        &.active {
            color: $color-minsk;
            @include hover {
                color: $color-alizarin;
            }
            &:after {
                opacity: 1;
            }
        }
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.mega__level  {
    .nav-link {
        @include media-to-lgtablet {
            text-decoration: none;
            font-size: $size-large;
            border: 0;
            padding: ($padding-default * 0.333) 0 (($padding-default * 0.5) - 2px);
            @include hover {
                color: $color-minsk;
            }
            @include pointer-coarse {
                color: $color-minsk;
            }
        }
    }
}