.header {
    z-index: 3;
    min-height: ($base-size * 2);
    background: $color-light;
    &.tran {
        @include transition;
    }
    @include media-from-lgtablet {
        position: relative;
        z-index: 12;
    }
    @include media-to-lgtablet {
        margin-top: 0 !important;
        padding-top: 0 !important;
        .global-reach {
            display: none!important;
        }
    }

    &__utils {
        /*//rtl:ignore*/
        float: left;
        padding-top: $padding-default / 2;

        > div {
            min-height: 2.4rem;
            display: inline-block;
            > a, > span, .icon-link{
                font-size: $size-regular;
                color: $color-minsk;
                text-decoration: none;
                display: inline-block;
                padding: 0 $padding-default;
                line-height: ($size-x-large * 1.2);
            }
            .global-reach__toggle {
                font-size: $size-x-large;
                border: none;
                background: none;
                border: thin dotted transparent;
                line-height: 1;
                &:focus {
                    border: thin dotted $color-alizarin;
                    outline-color: transparent;
                    padding-right: 0;
                    padding-left: 0;
                    margin-right: $margin-default;
                    margin-left: $margin-default;
                }     
            }      
            .icon-link {
                font-size: $size-x-large;
                border: none;
                background: none;
                border: thin dotted transparent;
                line-height: 1;
                &:focus {
                    border: thin dotted $color-alizarin;
                    outline-color: transparent;
                    padding-right: 0;
                    margin-right: $margin-default;
                }                  
            }
            &:last-of-type {
                border: none;
                > a {
                    padding-left: 0;
                }
            }

            &:not(.header__search) {
                display: none;
            }
        }        

        .header__lang{
            > a{
                color: $color-rivi-accessible2;
                padding: 0 $padding-default 0 0;
                &:last-child {
                    padding: 0 $padding-default;
                }
                @include transition;
                @include hover {
                    color: $color-minsk;
                }
            }
            > span {
                padding-left: 0; 
            }
        }

        .header__login {
            a {
                @include hover {
                    color: $color-alizarin;
                    text-decoration: underline;
                }
            }
        }
    }
    &__search-form {
        position: absolute;
        background: $color-light;
        width: 100%;
        display: none;

        &.open {
            display: block;
        }
    }
    @include media-from-lgtablet{
        &__utils {
            position: relative;
            overflow: hidden;
            > div {
                min-height: 2.4rem;
                display: inline-block;
                vertical-align: middle;
                border-left: 1px solid $color-rivi;
                &:not(.header__search) {
                    display: inline-block;
                }
            }
        }
        &__search-form {
            left: auto;
            right: 100%;
            bottom: 0;
            width: 339px;
            &.open {
                left: -1px;
                right: auto;
                @include rtl{
                    left: -4px;
                }
            }
        }
    }
}
.header-top {
    padding: $padding-default * 1.5 0 $padding-default;
    position: relative;
    &:after {
        content: '';
        display: inline-block;
        width: 100%;
        height: 0;
    }
    @include media-from-lgtablet{
        padding: 0;
        height: ($base-size * 4);
        .logo, .header__utils{
            position: absolute;
            top: 50%;
            -ms-transform: translateY(-50%);
            transform: translateY(-50%);
        }
        .logo{
            /*//rtl:ignore*/
            right: 0;
        }
        .header__utils{
            padding-top: 0;
            /*//rtl:ignore*/
            left: 0;
        }
    }
}
.menuOpen {
    overflow: hidden;
}
.menu-toggle,
.market-selector {
    display: none;
}
.h-search-container {
    float: left;
    button.icon-link {
        padding-left: 0;
        margin: 0;
        float: right;
        color: $color-minsk;
        @include media-to-lgtablet{
            line-height: normal;
        }
    }
    .searchBox {
        font-size: $size-regular;
        color: $color-minsk;
        padding: 2px ($padding-default / 2);
        padding-left: ($padding-default * 1.5);
        margin: 0;
        border: none;
        border-right: 1px solid $color-minsk;
        border-radius: 0px;
        margin-left: $margin-default / 2;
        outline: none;
        font-weight: 400;
        width: 100%;
        box-sizing: border-box;
        position: relative;
        background: transparent;
        @include placeholder{
            color: $color-minsk;
        }
    }
    span{
        .header__utils &{
            padding-left: 0;
        }
        &:hover{
            cursor:pointer;
        }
    }
    .h-search__close {
        position: absolute;
        /*//rtl:ignore*/
        left: 0;
        bottom: 0;
        color: $color-minsk;
        text-decoration: none;
        width:26px;
        height: 26px;
        line-height: 26px;
        .icon-close{
            position: absolute;
            bottom: 4px;
            left: 0;
            width: 20px;
            height: 18px;
            font-size: 18px;
            font-weight: 700;            
        }
        @include rtl {
            right: auto;
            left: 0rem;

            @include media-from-desktop {
                left: .5rem;
            }
        }
        &:hover{
            cursor: pointer;
        }
    }
    @include placeholder {
        color: $color-minsk;
        opacity: 0.5;
    }
}
.searchIcon {
    float: right;
    margin: 2px 6px 0 0;
}
.mobile-search-box {
    display: none;
}
.logo {
    /*//rtl:ignore*/
    float: right;
    padding: 0;
    margin: 0;
    max-width: 393px;
    position: relative;
    z-index: 1;
    > a {
        line-height: 0;
    }
    @include rtl{
        /*//rtl:ignore*/
        padding: 0 0 0 $padding-default;
        margin: 0;
        max-width: 393px;
        position: relative;
        z-index: 1;
        > a {
            line-height: 0;
        }
        @include media-to-lgtablet{
            float: right !important;
            padding: 0 ($padding-default / 2) 0 0;
        }
    }
}
.header-top .desktop-logo {
    display: block;
    overflow: hidden;
    img, svg {
        display: inline-block;
        width: auto;
        text-indent: -99em;
    }
}
.header-top .mobile-logo {
    display: none;
    max-width: 159px;
    min-height: 36px;
    text-indent: -200em;
    margin: $margin-default / 2 0 0;
    background-size: contain;
    &.hide {
        display: none;
    }
}
@include media-to-lgtablet {
    header{
        margin-top: 0 !important;
        padding-top: 0 !important;
        .global-reach {
            display: none!important;
        }
    }
    .cbp-hrmenu {
        display: none;
    }
    .header-top {
        display: block;
        position: relative;
        top:0 !important;
        right: 0;
        width: 100%;
        background: #fff;
        height: 2 * $base-size;
        margin: 0;
        padding: 0 0 0 $padding-default / 2;
        @include transition(all, .1s, 0s, linear);
        z-index: 11;
        min-width: 300px;
        padding-right: 0;
        .logo {
            margin: 10px 0;
        }
        .desktop-logo { 
            /* display: none;*/
        }
        .mobile-logo {
            display: block;
        }
    }
    .market-selector {
        display: none;
    }
    .h-search-container {
        position: absolute;
        left: $padding-default * 2;
        z-index: 2;
        top: 50%;
        width: (24px + $padding-default);
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        .icon-link {
            position: absolute;
            left: 0;
            z-index: 3;
            padding-left: 0;
        }
        &.open{
            width: auto;
            right: ($margin-default * 0.5);
        }
    }
}
.newsroom-highlighted-tags .tag-header {
    margin: ($margin-default / 2) ($margin-default / 4) 0;
    padding-top: 0;
    a {
        line-height: 1.5;
        &:hover{
            text-decoration: none;
            color: $color-alizarin;
        }
    }
}