%btn-primary, [class*="btn-primary"] {
    font-weight: 400;
    font-size: $size-regular;
    color: $btnPrimaryColor;
    background: transparent;
    border: 2px solid $btnPrimaryColor;
    padding: 1rem (3 * 1rem);
    display: inline-block;
    text-align: center;
    margin: 2rem 0;
    text-decoration: none;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
    z-index: 1;
    @include transition(all, .2s);
    @include media-small-mobile {
        width: 100%;
    }
    &:after {
        content: ""; 
        display: block;
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        height: 100%;
        width: 0;
        overflow: hidden; 
        background: $color-alizarin;
        box-sizing: border-box;
        @include transition(width, .2s);
        pointer-events: none;
        @include pointer-coarse{
            display: none;
        }
    }    
    span {
        display: none;
    }
    @include hover {
        color: $color-light;
        text-decoration: none; 
        @include pointer-coarse{
            color: $btnPrimaryColor;
        }
        &:after { 
            width: 100%;
        }
    }
}
%btn-primary-filled, .btn-primary-filled {
    background-color: $color-light;
}
%btn-primary-light, .btn-primary-light {
    color: $btnPrimaryLightColor;
    border-color: $btnPrimaryLightColor;
    &:after {
        background: $color-light;
    }
    @include hover {
        color: $color-minsk;
        text-decoration: none;
        @include pointer-coarse{
            color: $btnPrimaryLightColor;
        }
    }
}
.btn-primary-alt {
    color: $color-light;
    &:before {
        background: $color-light;
        content: "";
        display: block;
        position: absolute;
        top: 0;
        right:0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }
    @include pointer-coarse {
        background: $color-alizarin;
        &:before {
            display: none;
        }
    }
    &:after { 
        width: 100%;
    }
    @include hover {
        color: $color-alizarin;
        text-decoration: none;
        @include pointer-coarse {
            color: $color-light;
        }
        &:after {
            width: 0;
        }
    }
}
[type="submit"] {
    margin-top: ($margin-default * 2);
    margin-bottom: ($margin-default * 2);
    @include media-from-desktop {
        margin-bottom: ($margin-default * 4);
    }
}

%chips, .chips {    
    display: inline-block;
    color: $color-jacarta;
    padding: 0.75rem 3rem;
    font-size: $size-regular;
    text-decoration: none;
    border: 0.1rem solid $color-bison;
    border-radius: 0.5rem;
    @include hover {
        color: $color-light;
        background-color: $color-jacarta;
        border-color: $color-jacarta;
    }
}
%chips-with-close, .chips-with-close {
    position: relative;
    padding-left: 4rem;
    padding-right: 1.5rem;
    &:before,
    &:after {
        content: '';
        position: absolute;
        height: 0.1rem;
        width: 1.5rem;  
        background: $color-jacarta;
        top: 2rem;
        left: 1rem;                       
    }
    &:after {
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
    }
    &:before {        
        -ms-transform: rotate(-45deg);        
        transform: rotate(-45deg);
    }
    @include hover {
        &:before,
        &:after {
            background: $color-light;
        }
    }    
}

.btn-mini {
    @extend %btn-primary;
    padding: ($margin-default / 4) ($margin-default / 2);
    border-radius: 0;
}

.btn-small {
    @extend %btn-primary;
    border-radius: 0;
    text-shadow: none;
    padding: ($margin-default / 4) ($margin-default / 1.5);
    [class^="icon-"], [class*=" icon-"] {
        font-size: 12px;
        margin-left: 12px;
        float: right;
        margin-top: 4px;
    }
}

.btn-medium {
    @extend %btn-primary;
    border-radius: 0;
    display: block;
    text-shadow: none;
    width: 100%;
    [class^="icon-"], [class*=" icon-"] {
        font-size: 12px;
        margin-left: 12px;
        float: right;
        margin-top: 4px;
    }
}
