/* ================================= 1 FONT FAMILIES ================================= */

@font-face {
    font-family: 'GT-Walsheim-Pro';
    src: url(data:application/font-woff;charset=utf-8;base64,) format('woff');
    font-weight: 400;
    font-style: normal;
}

.text-left {
	text-align:right;
}

/* ================================= 2 FONT SIZING ================================= */
html{
    font-size:62.5%;
    -webkit-font-smoothing:antialiased;
}
@media only screen and( -webkit-min-device-pixel-ratio: 1.25 ),
       only screen and( min-resolution: 120dpi ),
       only screen and( min-resolution: 1.25dppx ) {

    body {
        @include font-size(10);
    }
} 
body {
    @include font-size(10);
    font-weight: 400;
    line-height:1.4;
}

body, body * {
    font-family: $font-Regular;
}

input, select, textarea {
    font-weight: 400;
}
%font-medium {
    font-weight: 300;
    font-style: normal;
}

%h1,h1,.h1 {
    @extend %font-medium;
    font-size: $size-gigantic;
    line-height: 80px;
    margin: 0 0 15px 0;

    @include media-only-mobile {
        font-size: $size-big;
        line-height: 56px;
    }
}
%h2,h2,.h2{
    @extend %font-medium;
    font-size: $size-big;
    line-height: 1.16666em;
    margin: 0 0 15px 0;

    @include media-only-mobile {
        font-size: $size-xx-large;
        line-height: 40px;
    }
}
%h3,h3,.h3,.big-text{
    @extend %font-medium;
    font-size: $size-x-large;
    line-height: 150%;
    margin: 0 0 15px 0;
}
h2, .h2{
    @extend %h3;
}
%h4,h4,.h4{
    @extend %font-medium;
    font-size: $size-large;
    line-height: 150%;
    margin: 0 0 15px 0;
}

%p,p {
    font-size: $size-regular;
    line-height: 150%;
    margin: 0 0 15px 0;
}
%li,li{
    font-size: $size-regular;
    line-height: 150%;
}
.bullet-list{
    li{
        margin-bottom: 1em;
    }
}
%p-big,.p-big{
    font-size: $size-large;
    line-height: 200%;
    font-weight: 400;
    margin: 0 0 15px 0;
}
%p-medium, %p-small, .p-medium, .small-text{
    font-size: $size-large;
    line-height: 150%;
    font-weight: 400;
    margin: 0 0 15px 0;
}
%p-small, .p-small, .small-text{
    font-size: $size-tiny;
}
.sectionTag {
    text-transform: uppercase;
    color: $color-rolling;
}
a {
    .sectionTag {
        color: inherit;
    }
}
%p-tag,.p-tag, .sectionTag, .feat-text{
    font-size: $size-tiny;
    line-height: 125%;
    font-weight: 400;
    letter-spacing: 0.2em;
    margin: 0 0 15px 0;
    a{
        color: inherit;
        text-decoration: none;
        @include hover{
            color: $color-alizarin;
        }
    }
}
%richtext-block,.richtext-block {
    h1 {
        @extend %h3;
    }
    p {
        line-height: 200%;
    }
}
.center-text {
    text-align:center;
}
a {
    text-decoration: none;
    color: $color-minsk;
    @include hover {
        color: $color-alizarin;
        text-decoration: underline;
    }
}
.article-banner-block {
    @include media-to-lgtablet {
        .description {
            a {
                text-decoration: underline;
                color: $color-light;
                @include hover {
                    color: $color-light;
                }
            }
        }        
    }    
}
.quote{
    margin-bottom: $margin-default;
}

.newsroom-headline,
.newsroom-section-header {
    font-family: $font-Regular!important;
}

@mixin pointer-coarse {
    @media (pointer: coarse) {
        @content;
    }
}