.form-title, %form-title {
    font-size: $size-large;
    display: block;
    padding-bottom: ($padding-default / 2);    
    padding-right: 0;
    margin-top: ($margin-default / 2);
    margin-bottom: 0;
    line-height: 150%;
}
.form-subtitle {
    font-size: $size-tiny;
    color: $color-alizarin;
    margin-top: (-$margin-default  * 0.25);
    padding-bottom: ($padding-default * 0.75);
}
%form-input, .form-input {
    display: block;
    border: 0.2rem solid $color-minsk;
    border-radius: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: $color-light;
    padding: 1rem 2rem;
    font-size: $size-regular;
    color: $color-minsk;    
    margin-bottom: ($margin-default / 4);    
    max-width: 100%;
    &.full-width {
        width: 100%;
    }
    @include media-only-mobile2 {
        width: 100%;
    }
}
.form-select, .selCont {
    @include form-select;
    margin-bottom: ($margin-default / 4);
    select::-ms-expand {
        display: none;
    }
    line-height: normal;
    .select-text {
        html[data-useragent*='MSIE 10.0'] & {
            display: none!important;
        }
        position: absolute;
        font-size: 1.6rem;
        pointer-events: none;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding-right: 2rem;
        padding-left: 3rem;
        line-height: 4.4rem;             
        .contact-form & {
            max-width: none;
            width: 100%;
            border: 0.2rem solid $color-minsk;
            background: $color-light;
            &.errors {
                border-color: $color-alizarin;
            }
        }
    }
    select {        
        color: rgba(1,1,1,0);
        width: 100%;
        html[data-useragent*='MSIE 10.0'] & {
            color: $color-light;
            border: 0.2rem solid #4F2D7F;
        }
        border: 0;
        .contact-form & {
            html[data-useragent*='MSIE 10.0'] & {
                color: $color-dark;
            }
        }
        .xformblock & {
            color: $color-dark;
        }
        option {
            color: $color-dark;
        }
    }
}
@include media-only-mobile2 {
    .form-input,.form-select {
        width: 100%;
    }
}
.form-button {
    display: block;
    padding: 1rem 2rem;
    margin-top: ($margin-default * 3);
    span::after {
        padding: 1rem 2rem;
    }
}
input[type="text"],
input[type="email"],
input[type="number"] {
    @extend %form-input;
    display: inline-block;
}
input[type="file"], .file_limit_info {
    font-size: $size-regular;
    font-weight: 400;
}
label {
    color: $color-dark;
    @extend %p;
    padding-right: $padding-default;
    @include hover{
        cursor: pointer;
    }
}
input[type="checkbox"] {
    opacity: 0;
    position: absolute;
    + label,
    + input[type="hidden"] + label {
        position: relative;
        padding-right: $padding-default;
        display: inline-block;
        &:before {
            content: '';
            position: absolute;
            right: 0rem;
            width: 2rem;
            height: 2rem;
            border: 0.1rem solid $color-minsk;
        }
        &:after {
            content: '';
            opacity: 0;
            transition: opacity 0.2s ease-in;
        }
    }
    &:checked + label,
    &:checked + input[type="hidden"] + label {
        &:after {
            content: '';    
            width: 1.4rem;
            height: 0.75rem;
            position: absolute;
            right: 3px;
            top: 0;
            margin-top: 0.4rem;
            border: 0.2rem solid $color-minsk;
            border-top: none;
            border-right: none;
            background: transparent;
            opacity: 1;
            -ms-transform: rotate(-55deg) skewX(-15deg);
            transform: rotate(-55deg) skewX(-15deg);
        }
    }    
}
input[type="radio"] {
    opacity: 0;
    position: absolute;
    + label {
        display: inline-block;
        position: relative;
        padding-right: 3rem;
        margin-left: 3rem;
         &:before {
            content: '';
            position: absolute;
            right: 0rem;
            margin-top: 0.6rem;
            width: 1rem;
            height: 1rem;
            border: 0.2rem solid $color-minsk;
            background: transparent;
            border-radius: 2rem;
        }
    }
    &:checked + label {
        &:before {
            background-color: $color-minsk;
            transition: background 0.2s ease-in;
        }
    }
}
textarea {
    @extend %form-input;
    display: inline-block;
    width: 100%;
}
input, textarea, select {    
    &.errors {
        border-color: $color-alizarin;
    }
}
.error-msg, .alert, .field-validation-error {
    color: $color-alizarin;
    font-weight: 700;
    ul {
        li {
            margin-bottom: ($margin-default / 2);
            margin-right: ($margin-default / 2)
        }
    }
}
input[type="file"] {
    background: $color-light;
    padding: ($padding-default / 4);
}
.file_limit_info {
    font-size: $size-tiny;
    margin-top: ($margin-default / 8);
}
.lrg-intro {
    @extend %h3;
}
.error-container {
   display: inline-block;
    .error {
        clip: rect(0 0 0 0);
        clip: rect(0, 0, 0, 0);
        margin: -1px;
        padding: 0;
        overflow: hidden;
        position: absolute;
        width: 1px;
        height: 1px; 
    }
}
.hpc, #HoneyPot {
    display: none;
}
.contact-form {
    input[type="text"],
    select {
        min-width: 20rem;
    }

    .form-select{
        .select-text {
            line-height: 3.8rem;
        }
        select {
            line-height: 2.2rem;
        }
    }
}
.xform {
    hr {
        margin: $margin-default 0;
    }
    .form-row {
        margin-bottom: ($margin-default / 2);        
        h2, label {
            @extend %form-title;            
        }                
        label {            
            margin-top: 0;            
            display: block;
            padding-right: 0;            
        }
        textarea {
            margin-bottom: ($margin-default / 2);
            max-width: 50rem;
            @include media-only-mobile2 {
                max-width: 100%;
            }
        }
        .horizontal-radio-elem {
            display: inline-block;
            label {
                  display: inline-block;  
                  padding-right: ($padding-default / 2);   
                  margin-left: $margin-default;
            }
            &.checkbox-elem {
                label {
                    padding-right: $padding-default;
                    margin-left: ($margin-default / 2);
                }
            }
        }
        .vertical-radio-row {
            label {
                padding-right: $padding-default;
                display: inline-block;
            }
        }
        input[type="submit"] {
            @extend %btn-primary;
            margin: $margin-default 0 ($margin-default / 2) 0;
            &:hover {
                background-color: $color-alizarin;
                @include pointer-coarse{
                    background-color: transparent;
                }
            }            
        }
    }
}
.xformblock {
    select {
        border: 0.2rem solid $color-minsk;
        html[data-useragent*='MSIE 10.0'] & {
            color: $color-dark;
        }
    }
}